import React from "react";
import { Input, message, Form, Modal, Icon } from "antd";
import { inject, observer } from 'mobx-react/index'
import { getUser } from "./../../utils/Session";

import NProgress from 'nprogress'
import 'nprogress/nprogress.css'
import api from "../../api/api";
import './index.css'
const Search = Input.Search;
@inject('headerStore') @observer

class Index extends React.Component {
    constructor(props) {
        super(props)
        this.props.headerStore.setCurrunt(this.props.location.pathname)

    }
    state = {
        list: [],
        visible: false,
        isEdit: false,
        editName: '编辑',
        pageNo: 1,
        pages: 1,
        backUserId:0
    }
    componentWillMount() {
        let userInfo = JSON.parse(getUser().split('?')[0])
        this.setState({
            backUserId: userInfo.userToken.backUserId,
            
        })
        this.getShieldList().then(res => {
            console.log(res)
            console.log(this.getList(res.data.list))
            if (res.data.success) {
                this.setState({
                    list: this.getList(res.data.list),
                    pages: res.data.pages,
                })
            }
        })
    }
    componentDidMount() {
        NProgress.done()
    }
    componentWillUnmount() {
        NProgress.start()
    }
    getShieldList = async (pageNo = 1, pageSize = 30) => {
        let res = api('getShieldList', 'post', { pageNo, pageSize })
        return res
    }
    handleOk = () => {
        this.setState({
            visible: true
        })
    }
    handleCancel = () => {
        this.setState({
            visible: false
        })
    }
    handleSave = () => {
        this.props.form.validateFields((err, values) => {
            if (!err) {
                api('saveShield', 'post', {}, {
                    Shield: values.name,
                    adminId:this.state.backUserId
                }).then(res => {
                    this.handleCancel()
                    this.props.form.resetFields()

                    if (!res.data.errorMessage) {
                        message.success('添加成功！')
                        this.getShieldList().then(res => {
                            console.log(res)
                            if (res.data.success) {
                                this.setState({
                                    list: this.getList(res.data.list),
                                    pages: res.data.pages,
                                })
                            }
                        })
                    } else {
                        message.warning(res.data.errorMessage)
                    }
                    
                })
            }
        });
    }
    getList = (val) => {
        if (val) {
            let arr = []
            let array = []

            val.map((res, index) => {
                if (index != 0 && index % 3 == 0) {
                    arr.push({ list: array })
                    array = []
                    array.push(res)
                } else {
                    if (val.length == index + 1) {
                        array.push(res)
                        arr.push({ list: array })
                        return
                    }
                    array.push(res)
                }
            })
            return arr
        }
    }
    setEidt = () => {
        if (!this.state.isEdit) {
            this.setState({
                editName: '取消',
                isEdit: true
            })
        } else {
            this.setState({
                editName: '编辑',
                isEdit: false
            })
        }
    }
    setDelete = (val) => {
        api('hideShield', 'post', {}, { 'searchKeyId': val }).then(res => {
            if (res.data.success) {
                message.success('删除成功！')
                this.getShieldList(this.state.pageNo).then(res => {
                    console.log(res)
                    console.log(this.getList(res.data.list))
                    if (res.data.success) {
                        this.setState({
                            list: this.getList(res.data.list),
                            pages: res.data.pages,
                        })
                    }
                })
            }
        })
    }
    setPrev = () => {
        if (this.state.pageNo == 1) {
            return
        } else {

            this.getShieldList(this.state.pageNo - 1).then(res => {
                this.setState({
                    pageNo: this.state.pageNo - 1
                })
                if (res.data.success) {
                    this.setState({
                        list: this.getList(res.data.list),
                        pages: res.data.pages,
                    })
                }
            })
        }
    }
    setNext = () => {
        if (this.state.pageNo == this.state.pages) {
            return
        } else {

            this.getShieldList(this.state.pageNo + 1).then(res => {
                this.setState({
                    pageNo: this.state.pageNo + 1
                })
                if (res.data.success) {
                    this.setState({
                        list: this.getList(res.data.list),
                        pages: res.data.pages,
                    })
                }
            })
        }
    }
    render() {
        const { getFieldDecorator } = this.props.form;
        return (
            <div id="system">
                <div className="container">
                    <div className="slider">
                        <div className="search">
                            <Search
                                placeholder="搜索"
                                onSearch={value => this.onSearch(value)}
                                style={{ width: '80%' }}
                                onChange={this.onChange}
                            />
                        </div>
                        <div className="title">
                            屏蔽词设置
                        </div>
                    </div>
                    <div className="content">
                        <div className="addBox">
                            <span onClick={this.handleOk}>+ 添加屏蔽词</span>
                            <div className="edit" onClick={this.setEidt}>{this.state.editName}</div>
                        </div>
                        <div className="listBox">
                            {this.state.list.map((res, index) => {
                                if (res) {
                                    return (<div className='list flexBox' key={index}>
                                        {res.list.map(res => {
                                            return <div className='flex' key={res.id}>
                                                <span>
                                                    {res.searchKey}
                                                    <span className={this.state.isEdit ? ' delete' : 'hide'} onClick={() => { this.setDelete(res.id) }}><Icon type="close" /></span>
                                                </span>
                                            </div>
                                        })}
                                    </div>)
                                }
                            })}
                            {
                                this.state.list.length == 0 ? <div className='noData'>
                                    <img src={require('./../../assets/images/img-nodata.png')} alt="" />
                                    <p>暂无相关文章</p>
                                </div> : ''
                            }
                            {
                                this.state.pages <= 1 ? '' : <div className='pagination'>
                                    <span onClick={this.setPrev}>
                                        <Icon type="caret-left" style={{ 'color': '#CCCCCC', 'fontSize': '16px' }} />
                                    </span>
                                    <span>
                                        {this.state.pageNo}/{this.state.pages}
                                    </span>
                                    <span onClick={this.setNext}>
                                        <Icon type="caret-right" style={{ 'color': '#CCCCCC', 'fontSize': '16px' }} />
                                    </span>
                                </div>
                            }
                        </div>
                    </div>
                </div>
                <Modal
                    title=""
                    visible={this.state.visible}
                    closable={false}
                    centered={true}
                    footer={null}
                    onOk={this.handleOk}
                    onCancel={this.handleCancel}
                    width='52%'
                    wrapClassName='add'
                >
                    <Form labelCol={{ span: 5 }} wrapperCol={{ span: 12 }}>
                        <Form.Item label='屏蔽词：'>
                            {getFieldDecorator('name', {
                                rules: [{ required: true, message: '请输入屏蔽词！' }],
                            })(
                                <Input
                                    placeholder="请输入屏蔽词"
                                    autocomplete="off"
                                />,
                            )}
                        </Form.Item>
                    </Form>
                    <div className="btn">
                        <span className='active' onClick={this.handleSave}>添加</span>
                        <span onClick={this.handleCancel}>取消</span>
                    </div>
                </Modal>
            </div>
        )
    }
}
const WrappedNormalLoginForm = Form.create({ name: 'normal_login' })(Index);
export default WrappedNormalLoginForm